import {useDispatch} from "react-redux";
import React, {useEffect} from "react";
import {setPageTitle} from "../features/common/headerSlice";
import ResponsiveAppBar from "../containers/home/ResponsiveAppBar";
import BottomNavigator from "../containers/home/BottomNavigator";
import {EventDetail} from "../features/homepage/event-detail";
import {useParams} from "react-router-dom";

const InternalPage = () => {
    const dispatch = useDispatch()
    const {id} = useParams();
    useEffect(() => {
        dispatch(setPageTitle({title: "Event Detail"}))
        document.title = "Event Detail";
    }, [dispatch])

    return (
        <>
            <ResponsiveAppBar/>
            <EventDetail id={id}/>
            <BottomNavigator/>
        </>
    )
}
export default InternalPage
