import {useEffect} from "react";
import {useNavigate} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import checkAuth from "../../../app/auth";
import Container from "@mui/material/Container";
import Loading from "../../../components/Loading/Loading";
import noImage from "../../../assets/image/No-Image-Placeholder.svg.webp"
import parse from "html-react-parser";
import {fetchDetailEventAction} from "../../../app/service/action/public/event/eventAction";

export const EventDetail = ({id}) => {
    const navigate = useNavigate()
    const dispatch: any = useDispatch();
    const token = checkAuth();

    const {data, isLoading} = useSelector(({eventDetailState}) => eventDetailState.eventDetail);

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchDetailEventAction(id));
    }, [dispatch, id]);

    const handleRegistration = (detailData) => {
        if (detailData.categoryEventId === 2) {
            window.location.href = `https://event.puhsbiketangerang.com/${detailData.id}`
        }
        if (token) {
            navigate(`/member/event/registration/${detailData.id}`, {state: detailData})
        } else {
            navigate(`/event/registration/${detailData.id}`, {state: detailData})
        }
    }
    return (
        <Container>
            {isLoading ? (
                <Loading/>
            ) : (
                <div className={'detail-event'}>
                    <div className="title">
                        {data.result.name}
                    </div>
                    <div className={'image'}>
                        {data.result.image ? (
                            <img src={data.result.image} alt=""/>
                        ) : (
                            <img src={noImage} alt=""/>
                        )}
                    </div>
                    <div className="note">
                        {parse(data.result.note)}
                    </div>
                    <div className={'button'}>
                        <button onClick={() => handleRegistration(data.result)}>
                            JOIN NOW
                        </button>
                    </div>
                </div>
            )}
        </Container>
    )
}
